/**
 * @description parse html
 *
 */

import { Descendant } from 'slate';
import { IDomEditor } from '@klein-editor/core';
import { VideoElement } from './custom-types';
import $, { DOMElement, getStyleValue } from '../utils/dom';

function genVideoElem(
  src: string,
  poster: string = '',
  width = '100%',
  height = '100%',
): VideoElement {
  return {
    type: 'video',
    src,
    poster,
    width,
    height,
    children: [{ text: '' }], // void 元素有一个空 text
  };
}

function parseHtml(
  elem: DOMElement,
  children: Descendant[],
  editor: IDomEditor,
): VideoElement {
  const $elem = $(elem);
  let src = '';
  let poster = '';
  let width = '100%';
  let height = '100%';

  // <iframe> 形式
  const $iframe = $elem;

  if ($iframe.length > 0) {
    width = $iframe.attr('width') || getStyleValue($iframe, 'width') || '100%';
    height =
      $iframe.attr('height') || getStyleValue($iframe, 'height') || '100%';
    src = $iframe[0].outerHTML;
    return genVideoElem(src, poster, width, height);
  }
  return genVideoElem(src, poster, width, height);
}
function parseVideoHtml(
  elem: DOMElement,
  children: Descendant[],
  editor: IDomEditor,
): VideoElement {
  const $video = $(elem);
  let src = '';
  let poster = '';
  let width = '100%';
  let height = '100%';

  // <video> 形式
  src = $video.attr('src') || '';

  console.log($video.attr('width'), $video.attr('height'), '$video');
  if (!src) {
    if ($video.length > 0) {
      const $source = $video.find('source');
      src = $source.attr('src') || '';
    }
  }
  width = $video.attr('width') || getStyleValue($video, 'width') || '100%';
  height = $video.attr('height') || getStyleValue($video, 'width') || '100%';
  poster = $video.attr('poster') || '';
  return genVideoElem(src, poster, width, height);
}

export const parseHtmlConf = {
  selector: 'iframe',
  parseElemHtml: parseHtml,
};

export const parseVideoHtmlConf = {
  selector: 'video',
  parseElemHtml: parseVideoHtml,
};
